@font-face {
  font-family: Helvetica Neue;
  src: url("HelveticaNeueLight.70e3e7ad.otf") format("opentype");
  font-weight: 300;
}

@font-face {
  font-family: Helvetica Neue;
  src: url("HelveticaNeueMedium.cc280065.otf") format("opentype");
  font-weight: 500;
}

.page__body {
  color: #333;
  background-color: #f7f7f7;
  flex-direction: column;
  gap: 110px;
  margin: 0;
  font-family: Inter, sans-serif;
  display: flex;
}

@media (width >= 768px) {
  .page__body {
    gap: 140px;
  }
}

@media (width >= 1280px) {
  .page__body {
    gap: 170px;
  }
}

.page__main {
  flex-direction: column;
  gap: 110px;
  display: flex;
}

@media (width >= 768px) {
  .page__main {
    gap: 140px;
  }
}

@media (width >= 1280px) {
  .page__main {
    gap: 170px;
  }
}

.page__menu {
  box-sizing: border-box;
  opacity: 0;
  pointer-events: none;
  background-color: #f7f7f7;
  height: 100vh;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  z-index: 2;
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding-inline: 30px;
}

@media (width >= 768px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1440px;
    padding-inline: 123px;
  }
}

.nav__list {
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #333;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  transition: font-weight .3s;
}

.nav__link:hover {
  font-weight: 700;
}

.top-bar {
  justify-content: space-between;
  align-items: center;
  height: 76px;
  font-family: Helvetica Neue, sans-serif;
  display: flex;
}

.top-bar__links {
  align-items: center;
  gap: 30px;
  display: flex;
}

.top-bar__tablet-pc {
  display: none;
}

@media (width >= 768px) {
  .top-bar__tablet-pc {
    align-items: center;
    gap: 30px;
    display: flex;
  }
}

.top-bar__buy {
  width: 100px;
  transition: transform .3s;
}

.top-bar__buy:hover {
  transform: scale(1.2);
}

.top-bar__menu, .top-bar__close {
  width: 32px;
  height: 32px;
  transition: transform .3s;
}

.top-bar__menu:hover, .top-bar__close:hover {
  transform: scale(1.2);
}

@media (width >= 1280px) {
  .top-bar__menu, .top-bar__close {
    display: none;
  }
}

.lang {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.link {
  text-decoration: none;
}

.link--specs {
  color: #4f4f4f;
  font-size: 14px;
  font-weight: 300;
  line-height: 16.93px;
}

.link--en-en {
  color: #333;
  pointer-events: none;
}

.link--en-ua {
  color: #828282;
}

.link--ua-ua {
  color: #333;
  pointer-events: none;
}

.link--ua-en {
  color: #828282;
}

.link--buy {
  color: #fff;
